import React from "react"
import Image from "gatsby-image"

const Card = ({ autor, testimonio, cargo, imagen }) => {
  return (
    <article className="testimonio">
      <div className="info">
        <Image fluid={imagen} className="image" />
        <p>{testimonio}</p>
      </div>
      <p className="footer">
        <span>{autor}</span>
        <span>{cargo}</span>
      </p>
    </article>
  )
}

export default Card
