import React from "react"
import styled from "@emotion/styled"
import { graphql, useStaticQuery } from "gatsby"

import Card from "./Card"

const TestimonialContainer = styled.section`
  width: 100vw;
  max-width: 1300px;
  padding: 25px 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  h2 {
    margin: 0 0 15px 0;
    font-weight: 500;
  }
  .testimonios {
    .testimonio {
      width: 90vw;
      height: 150px;
      background-color: var(--black);
      border-radius: 30px;
      color: var(--white);
      display: flex;
      flex-direction: column;
      margin-bottom: 10px;
      font-size: 1.1rem;
      padding: 10px;
      &:last-child {
        margin-bottom: 0;
      }
      .info {
        display: flex;
        align-items: center;
        .image {
          width: 30%;
          height: 100px;
          margin: 10px 10px 0 10px;
          border-radius: 40%;
        }
        p {
          width: 70%;
          margin: 0;
          text-align: justify;
        }
      }
      .footer {
        display: flex;
        flex-direction: column;
        align-items: center;
        margin: 0;
        span:first-of-type {
          font-weight: bold;
        }
      }
    }
  }
  @media (min-width: 424px) {
    .testimonios .testimonio {
      font-size: 1.2rem;
    }
  }
  @media (min-width: 499px) {
    .testimonios {
      display: flex;
      justify-content: space-around;
      flex-wrap: wrap;
      .testimonio {
        width: 48vw;
        height: 160px;
        font-size: 1rem;
      }
    }
  }
  @media (min-width: 499px) {
    .testimonios {
      .testimonio {
        width: 32vw;
        max-width: 650px;
      }
    }
  }
  @media (min-width: 1199px) {
    .testimonios .testimonio {
      font-size: 1.2rem;
    }
  }
  @media (min-width: 1439px) {
    .testimonios .testimonio {
      font-size: 1.3rem;
    }
  }
`

const Testimonial = () => {
  const {
    data: { nodes },
  } = useStaticQuery(graphql`
    query {
      data: allDatoCmsTestimonio {
        nodes {
          autor
          testimonio
          cargo
          imagen {
            fluid {
              ...GatsbyDatoCmsFluid
            }
          }
        }
      }
    }
  `)
  return (
    <TestimonialContainer>
      <h2>Testimonios</h2>
      <div className="testimonios">
        {nodes.map(({ autor, testimonio, cargo, imagen: { fluid } }, index) => (
          <Card
            key={`Testimonio-${index}`}
            autor={autor}
            testimonio={testimonio}
            cargo={cargo}
            imagen={fluid}
          />
        ))}
      </div>
    </TestimonialContainer>
  )
}

export default Testimonial
